import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import DisasterSummaryContent from '../component/DisasterSummary/DisasterSummaryContent'

class DisasterSummary extends Component {
  componentDidMount () {
    const {setDataLoaded, getDisasterSummary} = this.props

    setDataLoaded(false)
    getDisasterSummary()
  }

  render () {
    return (
      <main>
        <DisasterSummaryContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    disasterSummary: state.disasterSummary,
    disasterArea: state.disasterArea,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDisasterSummary: () => dispatch(actionCreators.getDisasterSummary()),
    disasterAreaChange: (disasterArea) => dispatch(actionCreators.disasterAreaChange(disasterArea)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(DisasterSummary)



