import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'

import DisasterInfoTable from './DisasterInfoTable/DisasterInfoTable'

const styles = () => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  formControl: {
    width: 'calc(100% - 1rem)',
    margin: '1rem .5rem'
  }
})

class DisasterSummaryContent extends Component {
  constructor () {
    super()
    this.state = {
      pagination: {
        rowsPerPage: 5,
        page: 0
      }
    }
  }

  handleChangePage = (event, page) => {
    this.setState({pagination: {
      ...this.state.pagination,
      page
    }})
  };

  handleChangeRowsPerPage = event => {
    this.setState({pagination: {
      ...this.state.pagination,
      rowsPerPage: event.target.value
    }})
  }

  render () {
    const {pagination} = this.state
    const {
      classes,
      disasterArea,
      disasterSummary,
      isDataLoaded,
      disasterAreaChange
    } = this.props
    const disasterAreaList = Object.keys(disasterSummary)

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded &&
              <div>
                <FormControl className={classes.formControl}>
                  <InputLabel htmlFor='disasterArea'>受災區</InputLabel>
                  <Select
                    value={disasterArea}
                    onChange={(event) => disasterAreaChange(event.target.value)}
                    inputProps={{id: 'disasterArea'}}>
                    <MenuItem value=''>
                      <em>None</em>
                    </MenuItem>
                    {
                      disasterAreaList.map(disasterArea => {
                        return (
                          <MenuItem key={disasterArea} value={disasterArea}>
                            {disasterArea}
                          </MenuItem>
                        )
                      })
                    }
                  </Select>
                </FormControl>
                <DisasterInfoTable
                  pagination={pagination}
                  infoList={disasterSummary[disasterArea] || []}
                  handleChangePage={this.handleChangePage}
                  handleChangeRowsPerPage={this.handleChangeRowsPerPage} />
              </div>
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(DisasterSummaryContent)



