import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import format from 'date-fns/format'

import Paper from '@material-ui/core/Paper'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TablePagination from '@material-ui/core/TablePagination'
import IconButton from '@material-ui/core/IconButton'

import CheckIcon from '@material-ui/icons/Check'
import LocationOnIcon from '@material-ui/icons/LocationOn'
import ClearIcon from '@material-ui/icons/Clear'

const styles = theme => ({
  root: {
    position: 'absolute',
    left: '2.5%',
    width: '95%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto'
  },
  table: {
    minWidth: 700
  },
  default: {
    width: '16.66%',
    padding: '4px 2rem'
  }
})

const DisasterInfoTable = (props) => {
  const { classes, infoList, pagination, handleChangePage, handleChangeRowsPerPage } = props
  const { rowsPerPage, page } = pagination

  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell style={{width: '10%', textAlign: 'center', padding: '1rem .5rem'}}>
              處理完成
            </TableCell>
            <TableCell className={classes.default}>災情類型</TableCell>
            <TableCell className={classes.default}>發生時間</TableCell>
            <TableCell className={classes.default}>案件地址</TableCell>
            <TableCell className={classes.default}>災情描述</TableCell>
            <TableCell style={{width: '10%', textAlign: 'center', padding: '1rem .5rem'}}>地圖</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {
            infoList
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((info, index) => {
                return (
                  <TableRow key={index}>
                    <TableCell style={{width: '10%', textAlign: 'center', padding: '1rem .5rem'}}>
                      {info.CaseComplete ? <CheckIcon /> : <ClearIcon />}
                    </TableCell>
                    <TableCell className={classes.default}>
                      {info.PName}
                    </TableCell>
                    <TableCell className={classes.default}>
                      {format(new Date(info.CaseTime), 'YYYY/MM/DD HH:mm:ss')}
                    </TableCell>
                    <TableCell className={classes.default}>
                      {info.CaseLocationDescription}
                    </TableCell>
                    <TableCell className={classes.default}>
                      {info.CaseDescription}
                    </TableCell>
                    <TableCell style={{width: '10%', textAlign: 'center', padding: '1rem .5rem'}}>
                      <IconButton
                        component='a'
                        target='blank'
                        href={`http://maps.google.com/maps?q=${info.Wgs84Y},${info.Wgs84X}`}>
                        <LocationOnIcon />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                )
              })
          }
        </TableBody>
      </Table>
      <div>
        <TablePagination
          component='div'
          count={infoList.length}
          rowsPerPage={rowsPerPage}
          page={page}
          backIconButtonProps={{'aria-label': 'Previous Page'}}
          nextIconButtonProps={{'aria-label': 'Next Page'}}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage} />
      </div>
    </Paper>
  )
}

export default withStyles(styles)(DisasterInfoTable)



